import React, { useEffect } from "react";

import "./SeoWebsite.css";

import Navbar from "../../../components/genral_components/Navbar/Navbar";

import SubCategoriesBanner from "../../../templates/subcategories/subcategoriesbanner/SubCategoriesBanner";

import seowebsite from "./seowebsite.jpg";

import SeoCardLeft from "../../../components/seo_website_design_components/seo_cards/seo_cards_left/SeoCardLeft";

import SeoCardsRight from "../../../components/seo_website_design_components/seo_cards/seo_cards_right/SeoCardsRight";

import Pricing from "../../../components/services_page_components/pricing/Pricing";

import Ourwork from "../../../components/home_page_components/ourwork/Ourwork";

import Skills from "../../../components/home_page_components/ourskills/Skills";

import Footer from "../../../components/genral_components/Footer/Footer";

import pic1 from "./pic1.png"
import pic2 from "./pic2.png"
import pic3 from "./pic3.png"
import pic4 from "./pic4.png"
import pic5 from "./pic5.png"
import pic6 from "./pic6.png"
import pic7 from "./pic7.png"
import pic8 from "./pic8.png"

import { useLocation } from "react-router-dom";

export default function SeoWebsite() {

  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  
  return (
    <div className="seo-website-main-container">
      <Navbar />

      <SubCategoriesBanner
        bannerHeading="SEO Website Optimization"
        bannerParagraph="Delivering effective SEO website optimization"
        bannerButton1="LET’S CHAT"
        bannerButton2="OUR WORK"
        bannerBackgroundImage={seowebsite}
      />

      <h1 className="interact-about-seo">
        Let's interac8 about <br /> SEO (Search Engine Optimization)
        optimization process
      </h1>

      <SeoCardLeft
        paragraph="At our digital marketing agency, we provide quality SEO optimization services that make your website stand out and drive exceptional traffic."
        button="LET’S CHAT"
        picture={pic1}
      />

      <SeoCardsRight
        heading="Keyword Research"
        paragraph="We look for relevant keywords and phrases that your target audience is likely to use when searching for information related to your website."
        button="LET’S CHAT"
        picture={pic2}
      />

      <SeoCardLeft
        heading="On-Page Optimization"
        paragraph="Various elements, such as title tags, meta descriptions, headings, and content, to are optimized on your website to make them more search engine-friendly and relevant to your target keywords."
        button="LET’S CHAT"
        picture={pic3}
      />

      <SeoCardsRight
        heading="Technical SEO "
        paragraph="Our website design agency makes sure that your website is properly crawled and indexed by search engines. This involves optimizing factors like site speed, mobile-friendliness, URL structure, XML sitemaps, and robots.txt files."
        button="LET’S CHAT"
        picture={pic4}
      />

      <SeoCardLeft
        heading="Content Creation"
        paragraph="Our talented content creators develop exceptional content that aligns with your target keywords and satisfies user intent. Mainly blog posts, articles, videos, infographics, and other forms of engaging content."
        button="LET’S CHAT"
        picture={pic5}
      />

      <SeoCardsRight
        heading="Link Building"
        paragraph="Our approach involves obtaining top-notch backlinks from trustworthy websites to enhance your website's authority and credibility in search engine evaluations. We accomplish this through various strategies, including guest posting, influencer outreach, content promotion, and other effective link-building techniques."
        button="LET’S CHAT"
        picture={pic6}
      />

      <SeoCardLeft
        heading="User Experience Optimization"
        paragraph="We enhance the overall user experience on your website, including easy navigation, intuitive design, clear calls-to-action, and fast-loading pages. A positive user experience can indirectly impact your SEO performance."
        button="LET’S CHAT"
        picture={pic7}
      />

      <SeoCardsRight
        heading="Analytics and Monitoring"
        paragraph="We use tools like Google Analytics to track and measure your website's performance, including organic traffic, keyword rankings, bounce rates, and conversion rates. Data is regularly analyzed to see where exactly the improvement is needed."
        button="LET’S CHAT"
        picture={pic8}
      />

      <p className="interact-about-seo-paragraph">
        SEO is an ongoing process that requires analysis, adaptation, and
        continuous monitoring to keep up with search engine algorithm updates
        and evolving user expectations. It's important to stay up-to-date with
        industry trends and best practices to maintain a strong online presence.
      </p>

      <br/>
      <br/>
      <br/>
      <br/>

      <Pricing/>

      <br/>
      <br/>
      <br/>

      <Skills/>

      <br/>
      <br/>
      <br/>

      <Ourwork/>
      <Footer/>

    </div>
  );
}
